import { Component, Directive, OnInit, ViewChild, TemplateRef, Output, EventEmitter } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Observable, BehaviorSubject, combineLatest } from 'rxjs';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { pluck, map, tap } from 'rxjs/operators';
import { PaymentRequestService, PaymentRequest, Account, RedirectUrl } from '../payment-request.service';
import { PaymentRequestError } from '../payment-request-error';

@Component({
  selector: 'bb-payment-request-container',
  templateUrl: './payment-request-container.component.html',
})
export class PaymentRequestContainerComponent implements OnInit {
  /**
   * Component context
   */
  readonly hostRef: PaymentRequestContainerComponent = this;
  /**
   * Emitter that fires when allowing payment
   */
  @Output() redirectToAllowUrl: EventEmitter<string> = new EventEmitter<string>();
  /**
   * Emitter that fires when rejecting payment
   */
  @Output() redirectToRejectUrl: EventEmitter<string> = new EventEmitter<string>();
  /**
   * Error notification template reference
   */
  @ViewChild('notificationError', { static: true })
  notificationErrorTemplate: TemplateRef<any> | string = '';

  /**
   * Stream of payment request ID
   */
  readonly paymentRequestId: Observable<string> = this.getRouteParam(this.route, 'paymentRequestId').pipe(
    map(paymentRequestId => paymentRequestId || ''),
  );
  /**
   * Stream of Payment request
   */
  readonly paymentRequest: Observable<PaymentRequest | undefined> = this.service.paymentRequest.pipe(
    tap((paymentRequest: PaymentRequest | undefined) => {
      if (paymentRequest && paymentRequest.selectedAccount) {
        this.selectedAccount = paymentRequest.selectedAccount;
      }
    }),
  );
  /**
   * Stream of loading spinner visibility flag
   */
  readonly loading: BehaviorSubject<boolean> = this.service.loading;
  /**
   * Stream of Payment request errors
   */
  readonly error: BehaviorSubject<PaymentRequestError | undefined> = this.service.error;
  /**
   * Selected account
   */
  selectedAccount: Account | undefined;
  /**
   * Show account error flag
   */
  showAccountError = false;
  /**
   * Stream of saving flag
   */
  readonly saving: BehaviorSubject<boolean> = this.service.saving;
  /**
   * Stream of saving errors
   */
  readonly savingError: BehaviorSubject<PaymentRequestError | undefined> = this.service.savingError;

  /**
   * Sends allow payment request
   */
  allowPaymentRequest(): void {
    this.showAccountError = false;
    if (this.selectedAccount) {
      this.service
        .allowPaymentRequest(this.selectedAccount, this.notificationErrorTemplate)
        .pipe(
          tap((redirect: RedirectUrl) => {
            if (redirect.redirectUrl) {
              this.redirectToAllowUrl.emit(redirect.redirectUrl);
            }
          }),
        )
        .subscribe();
    } else {
      this.showAccountError = true;
    }
  }

  /**
   * Sends reject payment request
   */
  rejectPaymentRequest(): void {
    this.service
      .rejectPaymentRequest(this.notificationErrorTemplate)
      .pipe(
        tap((response: RedirectUrl) => {
          if (response.redirectUrl) {
            this.redirectToRejectUrl.emit(response.redirectUrl);
          }
        }),
      )
      .subscribe();
  }

  ngOnInit() {
    this.service.getPaymentRequestFrom(this.paymentRequestId);
  }

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = combineLatest(
      route.paramMap.pipe(pluck<ParamMap, string | null>('params', param)),
      route.queryParamMap.pipe(pluck<ParamMap, string | null>('params', param)),
      (params, qparams) => {
        return params ? params : qparams;
      },
    );

    if (!route.parent) {
      return paramValue;
    }

    // ensure that level that has actual value keeps it from being cleared
    return combineLatest([paramValue, this.getRouteParam(route?.parent, param)]).pipe(
      map(params => params[0] || params[1]),
    );
  }

  constructor(private readonly service: PaymentRequestService, private readonly route: ActivatedRoute) {}
}

@Directive({
  selector: '[bbPaymentRequestWidgetCustomizable]',
})
export class PaymentRequestWidgetCustomizableDirective extends BbTemplate<
  PaymentRequestContainerComponent,
  PaymentRequest | null
> {}
