import { Component, Input, Output, EventEmitter, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

import { Account } from '../payment-request.service';

@Component({
  selector: 'bb-payment-request-account-selector',
  templateUrl: './payment-request-account-selector.component.html',
})
export class PaymentRequestAccountSelectorComponent {
  /**
   * Component context
   */
  readonly hostRef: PaymentRequestAccountSelectorComponent = this;
  /**
   * If error should be shown
   */
  @Input() showError = false;
  /**
   * List of accounts
   */
  @Input() accounts: Account[] = [];
  /**
   * Selected Account
   */
  @Input() selectedAccount: Account | undefined;
  /**
   * Emitter that fires when selecting account
   */
  @Output() selectedAccountChange: EventEmitter<Account> = new EventEmitter<Account>();

  /**
   * Select account event handler
   * @param account
   */
  onSelectAccount(account: Account): void {
    this.selectedAccountChange.emit(account);
    this.showError = false;
  }
}

@Directive({
  selector: '[bbPaymentRequestAccountSelectorCustomizable]',
})
export class PaymentRequestAccountSelectorCustomizableDirective extends BbTemplate<
  PaymentRequestAccountSelectorComponent,
  Account[]
> {}
