import { Injectable, TemplateRef } from '@angular/core';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Observable, ReplaySubject, BehaviorSubject, of } from 'rxjs';
import {
  catchError,
  pluck,
  filter,
  distinctUntilChanged,
  tap,
  switchMap,
  shareReplay,
  take,
  map,
} from 'rxjs/operators';
import { PaymentRequestsHttpService, PaymentRequest, Account } from '@backbase/data-ang/consent';
import { NotificationService } from '@backbase/ui-ang/notification';
import { parseError, PaymentRequestError } from './payment-request-error';

export { PaymentRequest, Account, Tpp } from '@backbase/data-ang/consent';

export interface RedirectUrl {
  redirectUrl?: string;
}

@Injectable()
export class PaymentRequestService {
  constructor(
    private readonly consentDataHttpService: PaymentRequestsHttpService,
    private readonly notification: NotificationService,
  ) {}

  private readonly paymentRequestId = new ReplaySubject<string>(1);

  readonly loading = new BehaviorSubject<boolean>(true);
  readonly error = new BehaviorSubject<PaymentRequestError | undefined>(undefined);

  readonly saving = new BehaviorSubject<boolean>(false);
  readonly savingError = new BehaviorSubject<PaymentRequestError | undefined>(undefined);

  readonly paymentRequest = this.paymentRequestId.pipe(
    filter((id: string) => !!id),
    distinctUntilChanged(),
    tap(() => this.loading.next(true)),
    switchMap((id: string) => this.loadPaymentRequestRequest(id)),
    tap(() => this.error.next(undefined)),
    catchError((error: PaymentRequestError) => {
      this.error.next(error);
      return of(undefined);
    }),
    tap(() => this.loading.next(false)),
    shareReplay(1),
  );

  getPaymentRequestFrom(paymentRequestId: Observable<string>) {
    paymentRequestId.subscribe(this.paymentRequestId);
  }

  allowPaymentRequest(account: Account, templateRef: TemplateRef<any> | string): Observable<RedirectUrl> {
    return this.paymentRequest.pipe(
      take(1),
      filter((paymentRequest: PaymentRequest | undefined) => !!paymentRequest),
      tap(() => this.saving.next(true)),
      switchMap((paymentRequest: PaymentRequest | undefined) => this.allow(paymentRequest as PaymentRequest, account)),
      tap(() => this.savingError.next(undefined)),
      catchError((error: PaymentRequestError) => this.handleError(error, templateRef)),
      tap(() => this.saving.next(false)),
    );
  }

  rejectPaymentRequest(templateRef: TemplateRef<any> | string): Observable<RedirectUrl> {
    return this.paymentRequest.pipe(
      take(1),
      filter((paymentRequest: PaymentRequest | undefined) => paymentRequest !== undefined),
      tap(() => this.saving.next(true)),
      switchMap((paymentRequest: PaymentRequest | undefined) => this.reject(paymentRequest as PaymentRequest)),
      tap(() => this.savingError.next(undefined)),
      catchError((error: PaymentRequestError) => this.handleError(error, templateRef)),
      tap(() => this.saving.next(false)),
    );
  }

  private handleError(error: PaymentRequestError, templateRef: TemplateRef<any> | string) {
    this.savingError.next(error);
    this.notification.showNotification({
      message: templateRef,
      modifier: 'error',
    });
    return of({});
  }

  private loadPaymentRequestRequest(id: string): Observable<PaymentRequest> {
    return this.consentDataHttpService.getIdByIdgetPaymentRequestById({ id }, 'response').pipe(
      pluck<HttpResponse<PaymentRequest>, PaymentRequest>('body'),
      catchError<PaymentRequest, never>((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private allow(
    { id, allowPaymentRedirectUrl: redirectUrl }: PaymentRequest,
    { id: accountId }: Account,
  ): Observable<RedirectUrl> {
    return this.consentDataHttpService.postConfirmById({ id, paymentPost: { accountId } }).pipe(
      map(() => ({ redirectUrl })),
      catchError((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private reject({ id, rejectPaymentRedirectUrl: redirectUrl }: PaymentRequest): Observable<RedirectUrl> {
    return this.consentDataHttpService.postRejectByIdpostReject({ id }).pipe(
      map(() => ({ redirectUrl })),
      catchError((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }
}
