import { HttpErrorResponse } from '@angular/common/http';

export class PaymentRequestAccessDenied {
  kind: 'PaymentRequestAccessDenied' = 'PaymentRequestAccessDenied';
  constructor(readonly error: HttpErrorResponse) {}
}

export class PaymentRequestNotFound {
  kind: 'PaymentRequestNotFound' = 'PaymentRequestNotFound';
  constructor(readonly error: HttpErrorResponse) {}
}

export class PaymentRequestBadRequest {
  kind: 'PaymentRequestBadRequest' = 'PaymentRequestBadRequest';
  constructor(readonly error: HttpErrorResponse) {}
}

export class PaymentRequestUnknownError {
  kind: 'PaymentRequestUnknownError' = 'PaymentRequestUnknownError';
  constructor(readonly error: HttpErrorResponse) {}
}

export class PaymentRequestConnectivityError {
  kind: 'PaymentRequestConnectivityError' = 'PaymentRequestConnectivityError';
  constructor(readonly error: HttpErrorResponse) {}
}

export type PaymentRequestError =
  | PaymentRequestAccessDenied
  | PaymentRequestNotFound
  | PaymentRequestBadRequest
  | PaymentRequestUnknownError
  | PaymentRequestConnectivityError;

export const parseError = (error: HttpErrorResponse): PaymentRequestError => {
  if (error.error instanceof ErrorEvent) {
    return new PaymentRequestUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new PaymentRequestConnectivityError(error);
      case 400:
        return new PaymentRequestBadRequest(error);
      case 401:
        return new PaymentRequestAccessDenied(error);
      case 403:
        return new PaymentRequestAccessDenied(error);
      case 404:
        return new PaymentRequestNotFound(error);
      default:
        return new PaymentRequestUnknownError(error);
    }
  }
};
