import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

export interface PaymentRequestAmountContext {
  amount: string | null;
  currency: string | null;
}

@Component({
  selector: 'bb-payment-request-amount',
  templateUrl: './payment-request-amount.component.html',
})
export class PaymentRequestAmountComponent {
  /**
   * Component context
   */
  readonly hostRef: PaymentRequestAmountComponent = this;
  /**
   * Amount of the request
   */
  @Input() amount = '';
  /**
   * Currency of the request
   */
  @Input() currency = '';
}

@Directive({
  selector: '[bbPaymentRequestAmountCustomizable]',
})
export class PaymentRequestAmountCustomizableDirective extends BbTemplate<
  PaymentRequestAmountComponent,
  PaymentRequestAmountContext
> {}
